//        - company-progress,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.company-progress {

  $block-name:                &; // #{$block-name}__element

  display: none;

  @media (min-width: $screen-sm) {
    @include row;
    // display: flex;
    max-width: 685px;
    text-align: center;
    margin: 60px 0;
  }

  &__item {
    @include col();
    @include col(sm, 4);
  }

  .icon {
    // height: 102px;

    @media (min-width: $screen-sm) {
      height: 102px;
    }
  }
  .value {
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 15px;
  }
  .name {
    font-size: 18px;
  }

}
